/* FONTS */

@font-face {
    font-family: 'SF-Pro-Regular';
    src: url("./assets/fonts/sf-pro/sf-pro-regular.ttf") format("truetype");
}

@font-face {
    font-family: 'Roboto-Regular';
    src: url("./assets/fonts/roboto/roboto-regular.woff") format("woff");
}

@font-face {
    font-family: 'Roboto-Medium';
    src: url("./assets/fonts/roboto/roboto-medium.woff") format("woff");
}

@font-face {
    font-family: 'Roboto-Bold';
    src: url("./assets/fonts/roboto/roboto-bold.woff") format("woff");
}


@font-face {
    font-family: 'Roboto-Light';
    src: url("./assets/fonts/roboto/roboto-light.woff") format("woff");
}

/* //FONTS */

html, body, #root, .App, .content-wrapper{
    height: 100% !important;
}


html,
body {
    margin: 0;
    padding: 0;
    font-family: 'SF-Pro-Regular';
    font-size: 10px;
}

code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
        monospace;
}

.content-wrapper {
    display: grid;
    grid-template-columns: minmax(160px, 1.5fr) 10.5fr;
    max-width: 100%;
    position: relative;
    font-size: 1.4rem;
}

.btn {
    border-radius: 7px;
    cursor: pointer;
    transition: all .2s linear;
    font-size: 1.4rem;
}

.color_grey {
    color: #929498;
}

.text_sf-pro {
    font-family: 'SF-Pro-Regular';
}

.color_blue {
    color: #4377DE;
}

.border-top_grey {
    border-top: 1px solid #A2A2A2 !important;
}

/* GLOBAL CLASSES */
 .color_light-grey {
	 color: #c5c5c5;
}
 .text_no-br {
	 white-space: nowrap;
}

@media screen and (max-width: 450px) {

    html,
    body {
        font-size: 7px;
    }

    .content-wrapper {
        grid-template-columns: minmax(125px, 1.5fr) 10.5fr;
        max-width: 100%;
    }
}




